document.addEventListener("DOMContentLoaded", () => {
    const submitButton = document.getElementById("submit-to-llm");
    const responseArea = document.getElementById("llm-response-area");
    const responseHtmlPreview = document.getElementById("llm-response-html-preview");
    const replyButton = document.getElementById("reply-with-content");
    const discardButton = document.getElementById("discard-response");
    const statusMessage = document.getElementById("status-message");
    const actionSelect = document.getElementById("action-select");
    const userQueryInput = document.getElementById("user-query-input");
  
    // New elements for the collapsible section
    const toggleOptionsButton = document.getElementById("toggle-options");
    const aiOptionsContent = document.getElementById("ai-options-content");
    const toggleArrow = document.getElementById("toggle-arrow");
  
    let lastLlmResponse = ""; // Store the last LLM response for the reply button
  
    // --- Collapsible Section Logic ---
    // Initial state: START EXPANDED
    aiOptionsContent.classList.add("expanded"); // Add 'expanded' class to start expanded
    toggleArrow.classList.add("expanded"); // Rotate arrow up initially

    toggleOptionsButton.addEventListener("click", () => {
        aiOptionsContent.classList.toggle("expanded");
        toggleArrow.classList.toggle("expanded");
    });
    // --- End Collapsible Section Logic ---
  
  
    // Ensure Office.js is fully loaded before interacting with mailbox properties
    Office.onReady(async (info) => {
      if (info.host === Office.HostType.Outlook) {
        console.log("Office.js is ready for Outlook host.");
        // You can put any initial setup here if needed
      }
    });
  
    submitButton.addEventListener("click", async () => {
        responseHtmlPreview.innerHTML = ""; // Clear the HTML preview
        lastLlmResponse = ""; // Clear the stored response
        aiOptionsContent.classList.remove("expanded");
        toggleArrow.classList.remove("expanded");
        try {
            statusMessage.textContent = "‚è≥ Extracting email body and user info...";
            
            // Get the current user's email address
            const currentUserEmail = Office.context.mailbox.userProfile.emailAddress;
            console.log("Current user email:", currentUserEmail);
  
            // Get the selected action from the dropdown
            const selectedAction = actionSelect.value;
            console.log("Selected action:", selectedAction);
  
            // Get the custom query text
            const customQueryText = userQueryInput.value.trim(); // .trim() removes leading/trailing whitespace
            console.log("Custom query:", customQueryText);
  
            // Determine the base query based on the selected action
            let llmPrompt = "";
            if (selectedAction === "reply") {
                llmPrompt = "Please generate a reply to this email. ";
            } else if (selectedAction === "summarize") {
                llmPrompt = "Please summarize this email concisely. ";
            }
  
            // Append custom query if provided
            if (customQueryText) {
                llmPrompt += `Additionally, consider this instruction: "${customQueryText}".`;
            }
  
            await Office.context.mailbox.item.body.getAsync("html", async result => { // Ensure "html" is requested
                if (result.status === Office.AsyncResultStatus.Succeeded) {
                    const emailBody = result.value;
  
                    const payload = {
                        email_body: emailBody,
                        query: llmPrompt, // Use the dynamically generated combined query
                        email_addr: currentUserEmail,
                        action: selectedAction // Include the new 'action' field
                    };
  
                    statusMessage.textContent = "üì° Sending request to LLM...";
  
                    const res = await fetch("http://localhost:8082/api/ask", {
                        method: "POST",
                        headers: {
                            "Content-Type": "application/json"
                        },
                        body: JSON.stringify(payload)
                    });
  
                    if (!res.ok) {
                        const errorText = await res.text(); // Get more details from the error response
                        throw new Error(`Server responded with status ${res.status}: ${errorText}`);
                    }
  
                    const data = await res.json();
                    const llmResponse = data.result?.answer || "‚ö†Ô∏è No response received.";
                    
                    // Store the response for the reply button
                    lastLlmResponse = llmResponse; 
  
                    // Assign the HTML directly to the preview div
                    responseHtmlPreview.innerHTML = llmResponse; 
                    responseArea.classList.remove("hidden"); // Remove the 'hidden' class
                    responseArea.classList.add("visible");   // Add the 'visible' class
                    statusMessage.textContent = "‚úÖ Response received!";
  
                    // Clear the custom query input after successful submission
                    userQueryInput.value = ""; 
  
                } else {
                    throw new Error("Failed to extract email body: " + result.error.message);
                }
            });
        } catch (err) {
            console.error("Error in taskpane:", err);
            statusMessage.textContent = "‚ùå Error: " + err.message;
        }
    });
  
    replyButton.addEventListener("click", () => {
        // Use the stored LLM response for the reply draft.
        // If the LLM generates a summary, this will place the summary in the reply draft.
        // You might consider adding a condition here to only call displayReplyForm if action was 'reply'.
        Office.context.mailbox.item.displayReplyForm(lastLlmResponse);
    });
  
    discardButton.addEventListener("click", () => {
        responseArea.classList.remove("visible"); // Remove the 'visible' class
        responseArea.classList.add("hidden");    // Add the 'hidden' class back
        responseHtmlPreview.innerHTML = ""; // Clear the HTML preview
        lastLlmResponse = ""; // Clear the stored response
        userQueryInput.value = ""; // Clear the custom query input on discard
        statusMessage.textContent = "üóëÔ∏è Response discarded.";
        aiOptionsContent.classList.toggle("expanded"); // Add 'expanded' class to start expanded
        toggleArrow.classList.toggle("expanded"); // Rotate arrow up initially
    });
  });